import Popup from "reactjs-popup";
export default function ServiceDetail({
  trigger,
  service,
  open = false,
  onClose,
}) {
  return (
    <Popup
      trigger={trigger}
      modal
      contentStyle={{ backgroundColor: "#eff4f9", borderRadius: "20px" }}
      open={open}
      onClose={onClose}
    >
      {(close) => (
        <div>
          <div onClick={() => close()}>Close</div>
        </div>
      )}
    </Popup>
  );
}
